<template>
  <div>
    <div class="bgackground ">
      <div class=" container d-flex justify-content-between pt-5 pb-4">
        <div><h1 class="h3 text-dark ">Pomodoro</h1></div>
      </div>

      <div class="container-fluid borderTOP"></div>

      <!-- 音樂尾巴 -->
      <!-- 內容開始 -->
      <div class="container my-5">
        <div class="row">
          <!-- 倒數開始 -->
          <!-- d-flex justify-content-center -->
          <div class="col-6">
            <div class=" mb-3">
              <p class="h1 text-center pr-1 text-monospace text-dark">{{ titleName.name }}</p>
            </div>
            <div class="borderCircle" style="margin:0 auto;position:relative">
              <div
                class="inner  d-flex justify-content-center align-items-center align-self-center "
                style="font-size:100px"
              >
                <div>
                  <span>{{ minutes }} </span>
                </div>
                <div class="mx-3 mb-3">
                  <span>: </span>
                </div>
                <div>
                  <span>{{ seconds }} </span>
                </div>
              </div>

              <div class=" btn icon-Position">
                <i @click="clear()" class="  far fa-stop-circle fa-3x"></i>
              </div>
            </div>

            <div class="mt-4 d-flex justify-content-center  align-items-center ">
              <!-- STOP重來 -->

              <div class="btn  ">
                <button
                  v-if="!isStart"
                  @click="start()"
                  type="button"
                  class="px-5  pb-2  btn btn-outline-primary iconPlay "
                >
                  Start
                </button>
                <button
                  v-else
                  type="button"
                  @click="start()"
                  class="px-5 pb-2  btn btn-outline-danger iconPlay"
                >
                  Pause
                </button>
              </div>
            </div>
          </div>

          <!-- TODOLIST -->
          <div class="col-6 pl-4 mt-2">
            <div class="input-group mb-3 w-75 ml-2 ">
              <input
                type="text"
                style="border-radius:25px;"
                class="form-control py-4 "
                placeholder="Add New Task"
                maxlength="15"
                aria-label="Add New Task"
                aria-describedby="button-addon2"
                v-model="newtodo"
              />
              <div class="ml-5">
                <div
                  type="button"
                  @click.prevent="addtodo()"
                  class="btn btn-dark py-2 mt-1"
                >
                  <i class="fas fa-plus"></i>
                </div>
              </div>
            </div>

            <div class="border-top"></div>
            <h2 class="my-2 mb-3  ml-2 text-dark">To Do List</h2>

            <table class="table table-hover  bg-white border">
              <thead>
                <tr class="font-weight-bolder">
                  <th class="pl-3" scope="col">Task Name</th>
                  <th scope="col">More</th>
                </tr>
              </thead>

              <tbody>
                <tr v-for="(item, index) in todos" :key="index">
                  <th scope="row" width="350">
                    <div v-if="item.id !== cacheTodo.id">
                      <span class="ml-2 text-dark" @click="select(item)" style="font-size:20px">
                        {{ item.title }}
                      </span>
                    </div>

                    <div v-else>
                      <div  style="width:70%">
                        <input
                          class="form-control"
                          type="text"
                          style="border-radius:30px;"
                          maxlength="15"
                          size="20"
                          id="inputPassword2"
                          placeholder="task name"
                          v-model="cacheTitle"
                          @keyup.esc="cancelEdit()"
                          required
                        />
                        <span
                          v-if="isShow"
                          style="font-size:15px"
                          class="text-danger pl-2"
                          >不可輸入空白
                        </span>
                      </div>
                    </div>
                  </th>
                  <td>
                    <div v-if="item.id !== cacheTodo.id">
                      <div v-if="item.id !== titleName.id">
                        <i
                          type="button"
                          @click="editTodo(item)"
                          class="fas fa-pen-nib text-dark"
                        ></i>
                        <i
                          type="button"
                          v-if="todos.length > 1"
                          @click="removeTodo(index, item)"
                          class=" fas fa-trash pl-3 text-danger"
                        ></i>
                        <i v-else class=" fas fa-trash pl-3 text-dark "></i>
                      </div>
                      <div v-else>
                        <i
                          type="button"
                          @click="editTodo(item)"
                          class="fas fa-pen-nib text-dark"
                        ></i>
                        <!-- <i  class=" fas fa-trash pl-3 text-dark "></i> -->
                      </div>
                    </div>
                    <div v-else class="pt-2">
                      <i
                        type="submit"
                        @click="EnterEdit(item)"
                        class="fas fa-check text-success"
                      ></i>
                      <i
                        type="button"
                        @click="cancelEdit()"
                        class=" fas fa-undo-alt pl-3 text-dark"
                      ></i>
                    </div>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
        <!-- row的尾巴 -->
      </div>
      <div class="container-fluid borderTOP"></div>

      <div class=" text-center pt-3 pb-4">
        <span class="h4">copyright@2021 By Yang</span> <br />
        <span class="h6"
          >(資料來源來自網路，僅個人練習用，非商業/營利網站)</span
        >
       
      </div>
    </div>
  </div>
</template>

<script>
import $ from "jquery";
export default {
  name: "HelloWorld",
  data() {
    return {
      newtodo: "",
      todos: [{ id: "TheFirstTask", title: "TheFirstTask" }],
      isStart: false,
      cacheTodo: {},
      cacheTitle: "",
      titleName: { name: "TheFirstTask", id: "TheFirstTask" },
      minutes: "25",
      seconds: "00",

      setSecond: 1500, //初始共幾秒
      interval: "", //給予id

      istitle: false,
      isShow: false
    };
  },

  methods: {
    addtodo() {
      const vm = this;

      var value = this.newtodo.trim();
      // 賦予 id - 將當下的時間轉為數字
      var timestamp = Math.floor(Date.now());

      if (!value) {
        return;
      }
      this.todos.push({ id: timestamp, title: value });
      this.newtodo = "";
    },
    removeTodo(index, item) {
      if (item.id == this.titleName.id) {
        alert("gogog");
      } else {
        console.log(this.todos.length);
        if (this.todos.length > 1) {
          this.todos.splice(index, 1);
        }
      }
    },
    editTodo(item) {
      this.cacheTodo = item;
      this.cacheTitle = item.title;
    },
    cancelEdit() {
      this.cacheTodo = {};
    },
    EnterEdit(item) {
      // console.log(this.cacheTitle)
      var value = this.cacheTitle.trim();
      console.log(value);
      this.isShow = false;
      if (value) {
        this.isShow = false;
        item.title = this.cacheTitle;
        this.cacheTitle = "";
        this.cacheTodo = {};
        if (this.titleName.id == item.id) {
          this.titleName.name = item.title;
        }
      } else {
        this.isShow = true;
        return;
      }
    },

    select(item) {
      if (this.titleName.name == item.title) {
        this.titleName.name = item.title;
        this.titleName.id = item.id;
      } else {
        this.titleName.name = item.title;
        this.titleName.id = item.id;
        this.clear();
      }
    },

    start() {
      let vm = this;
      if (this.isStart) {
        clearInterval(this.interval);
        // this.$refs.audio.pause()
      } else {
        this.interval = setInterval(function() {
          vm.counter();
        }, 1000);
        // this.$refs.audio.play()
      }
      this.isStart = !this.isStart;
    },
    clear() {
      // this.$refs.audio.load()
      clearInterval(this.interval);
      this.setSecond = 1500;
      this.minutes = "25";
      this.seconds = "00";
      this.isStart = false;
    },
    // 計算時間
    counter() {
      let min = Math.floor(Math.floor(this.setSecond % 3600) / 60);
      let sec = this.setSecond % 60;
      if (this.setSecond > 0) {
        this.setSecond -= 1;

        // 分數格式調整
        if (min < 10) {
          this.minutes = "0" + min;
        } else {
          this.minutes = min;
        }

        // 秒數格式調整
        if (sec < 10) {
          this.seconds = "0" + sec;
        } else {
          this.seconds = sec;
        }
      } else {
        this.clear();
      }
    },
    www(name){
  return `Hi there, ${name}`

    }
  }, //' 方法尾巴

  created() {
this.www()

  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.borderTOP {
  border: 3px #d09661 dashed;
}

</style>



